<template>
    <div
        class="rhythm-password-container"
        @click="startGame"
        v-if="!gameStarted"
    >
        <div class="start-screen">
            <h1 class="neon-title">ğŸ® èŠ‚å¥å¯†ç </h1>
            <p class="subtitle">å²ä¸Šæœ€åäººç±»çš„å¯†ç è¾“å…¥ä½“éªŒ</p>
            <div class="password-hint">
                <p>
                    ä½ çš„å¯†ç æ˜¯ï¼š<span class="password-text">{{
                        targetPassword
                    }}</span>
                </p>
            </div>
            <button class="start-button">ç‚¹å‡»å¼€å§‹æŒ‘æˆ˜</button>
            <div class="tips">
                <p>ğŸ’¡ æç¤ºï¼šè·ŸéšéŸ³ä¹èŠ‚å¥å‡»æ‰“å­—ç¬¦</p>
                <p>âš ï¸ å¿…é¡»æŒ‰æ­£ç¡®é¡ºåºå®Œæˆå¯†ç è¾“å…¥</p>
            </div>
        </div>
    </div>

    <div class="rhythm-password-game" v-else-if="gameStarted && !gameSuccess">
        <!-- é¡¶éƒ¨å¯†ç æ˜¾ç¤º -->
        <div class="top-section">
            <div class="password-display">
                <span class="label">ç›®æ ‡å¯†ç ï¼š</span>
                <span
                    v-for="(char, index) in targetPassword"
                    :key="index"
                    :class="[
                        'password-char',
                        {
                            completed: index < currentPasswordIndex,
                            current: index === currentPasswordIndex,
                        },
                    ]"
                >
                    {{ char }}
                </span>
            </div>
            <div class="encouragement">{{ encouragementText }}</div>
        </div>

        <!-- ä¸­é—´æ¸¸æˆåŒºåŸŸ -->
        <div class="game-area" ref="gameArea">
            <!-- éŸ³ç¬¦è½¨é“ -->
            <div class="tracks-container">
                <div v-for="track in tracks" :key="track.id" class="track">
                    <!-- åˆ¤å®šçº¿ -->
                    <div class="judge-line"></div>
                </div>
            </div>

            <!-- ä¸‹è½çš„éŸ³ç¬¦ -->
            <div
                v-for="note in activeNotes"
                :key="note.id"
                :class="['note', note.type, { target: note.isTarget }]"
                :style="{
                    left: note.x + 'px',
                    top: note.y + 'px',
                    height: note.height + 'px',
                }"
                @mousedown="handleNoteClick(note, $event)"
                @touchstart.prevent="handleNoteClick(note, $event)"
            >
                <span class="note-char">{{ note.char }}</span>
            </div>

            <!-- ç‚¹å‡»ç‰¹æ•ˆ -->
            <div
                v-for="effect in hitEffects"
                :key="effect.id"
                :class="['hit-effect', effect.type]"
                :style="{ left: effect.x + 'px', top: effect.y + 'px' }"
            >
                <span class="effect-text">{{ effect.text }}</span>
            </div>
        </div>

        <!-- åº•éƒ¨çŠ¶æ€æ  -->
        <div class="bottom-section">
            <div class="progress-display">
                <span class="label">è¿›åº¦ï¼š</span>
                <span
                    v-for="(char, index) in targetPassword"
                    :key="index"
                    :class="[
                        'progress-char',
                        { done: index < currentPasswordIndex },
                    ]"
                >
                    {{ index < currentPasswordIndex ? char : "_" }}
                </span>
            </div>

            <div class="combo-display" :class="{ fever: combo >= 5 }">
                <div class="combo-number">{{ combo }}</div>
                <div class="combo-label">COMBO</div>
            </div>

            <div class="accuracy-display">
                <div class="accuracy-label">{{ lastJudgement }}</div>
                <div class="hp-bar">
                    <div class="hp-fill" :style="{ width: hp + '%' }"></div>
                </div>
            </div>
        </div>

        <!-- å¤±è´¥æç¤º -->
        <div class="game-over-overlay" v-if="gameFailed">
            <div class="game-over-content">
                <h2>ğŸ’” å†æ¥ä¸€æ¬¡å§~</h2>
                <p class="fail-message">{{ failMessage }}</p>
                <div class="stats">
                    <p>å‡†ç¡®ç‡ï¼š{{ accuracy }}%</p>
                    <p>æœ€é«˜è¿å‡»ï¼š{{ maxCombo }}</p>
                </div>
                <button class="retry-button" @click="restartGame">
                    é‡æ–°æŒ‘æˆ˜
                </button>
            </div>
        </div>
    </div>

    <!-- æˆåŠŸç•Œé¢ -->
    <div class="success-screen" v-else-if="gameSuccess">
        <div class="fireworks"></div>
        <div class="success-content">
            <h1 class="success-title">ğŸŠ æ­å–œä½ ï¼ç»ˆäºè¿›æ¥äº†ï¼</h1>
            <div class="success-stats">
                <div class="stat-item">
                    <div class="stat-value">{{ accuracy }}%</div>
                    <div class="stat-label">å‡†ç¡®ç‡</div>
                </div>
                <div class="stat-item">
                    <div class="stat-value">{{ maxCombo }}</div>
                    <div class="stat-label">æœ€é«˜è¿å‡»</div>
                </div>
                <div class="stat-item">
                    <div class="stat-value">{{ attempts }}</div>
                    <div class="stat-label">å°è¯•æ¬¡æ•°</div>
                </div>
            </div>
            <div class="rating">
                <div class="rating-text">{{ getRating() }}</div>
            </div>
            <button class="continue-button" @click="handleLoginSuccess">
                ç¡®è®¤ç™»å½•
            </button>
        </div>
    </div>
</template>

<script setup>
import { ref, onMounted, onUnmounted, computed } from "vue";

// Props
const emit = defineEmits(["success"]);

// æ¸¸æˆçŠ¶æ€
const gameStarted = ref(false);
const gameFailed = ref(false);
const gameSuccess = ref(false);
const targetPassword = ref("happeny");
const currentPasswordIndex = ref(0);
const attempts = ref(0);

// éŸ³ç¬¦å’Œè½¨é“
const tracks = ref([
    { id: 1, x: 0 },
    { id: 2, x: 1 },
    { id: 3, x: 2 },
    { id: 4, x: 3 },
    { id: 5, x: 4 },
]);
const activeNotes = ref([]);
const noteIdCounter = ref(0);

// æ¸¸æˆæ•°æ®
const combo = ref(0);
const maxCombo = ref(0);
const hp = ref(100);
const lastJudgement = ref("");
const hitEffects = ref([]);
const effectIdCounter = ref(0);

// ç»Ÿè®¡æ•°æ®
const perfectHits = ref(0);
const goodHits = ref(0);
const missHits = ref(0);

// éŸ³é¢‘ç›¸å…³
let audioContext = null;
let audioElement = null;
let beatSchedule = [];
let gameAreaRef = ref(null);
let gameLoop = null;
let currentBeatIndex = 0;
let gameStartTime = 0;
let longPressNote = null;
let longPressTimer = null;

// é¼“åŠ±æ–‡æ¡ˆ
const encouragementTexts = [
    "åŠ æ²¹å•Šæœ‹å‹ï¼Œä½ è‚¯å®šè¡Œçš„ï¼",
    "è·ŸéšèŠ‚å¥ï¼Œä½ èƒ½åšåˆ°ï¼",
    "æ‰‹é€Ÿupï¼èŠ‚å¥æ„Ÿupï¼",
    "å°±æ˜¯è¿™æ ·ï¼Œç»§ç»­ä¿æŒï¼",
    "éŸ³ä¹åœ¨ä½ å¿ƒä¸­ï¼",
];
const encouragementText = ref(encouragementTexts[0]);

// å¤±è´¥æ–‡æ¡ˆ
const failMessages = [
    "èŠ‚å¥ä¸å¯¹å“¦~",
    "å¯†ç ï¼šhappenyï¼Œè®°ä½äº†å—ï¼Ÿ",
    "è¦ä¸è¦æˆ‘æ”¾æ…¢ç‚¹ï¼Ÿï¼ˆå¹¶ä¸ä¼šï¼‰",
    "å»ºè®®å…ˆå»ç»ƒç»ƒæ‰‹é€Ÿ",
    "ä½ çš„æ‰‹æŒ‡è¿˜å¥½å—ï¼Ÿ",
    "èŠ‚å¥æ„Ÿéœ€è¦ç»ƒä¹ å“¦~",
    "å¥¶å¥¶éƒ½æ¯”ä½ å¿«",
];
const failMessage = ref("");

// è®¡ç®—å‡†ç¡®ç‡
const accuracy = computed(() => {
    const total = perfectHits.value + goodHits.value + missHits.value;
    if (total === 0) return 100;
    return Math.round(((perfectHits.value + goodHits.value) / total) * 100);
});

// åˆå§‹åŒ–æ¸¸æˆ
const startGame = () => {
    gameStarted.value = true;
    gameFailed.value = false;
    currentPasswordIndex.value = 0;
    combo.value = 0;
    hp.value = 100;
    activeNotes.value = [];
    hitEffects.value = [];
    attempts.value++;

    // æ’­æ”¾èƒŒæ™¯éŸ³ä¹
    playBackgroundMusic();

    // ç”ŸæˆéŸ³ä¹èŠ‚æ‹ï¼ˆæ¨¡æ‹Ÿbase.wavçš„èŠ‚å¥ï¼‰
    generateBeats();

    // å¼€å§‹æ¸¸æˆå¾ªç¯
    gameStartTime = Date.now();
    currentBeatIndex = 0;
    gameLoop = setInterval(updateGame, 1000 / 60); // 60 FPS

    // å®šæœŸæ›´æ¢é¼“åŠ±æ–‡æ¡ˆ
    setInterval(() => {
        encouragementText.value =
            encouragementTexts[
                Math.floor(Math.random() * encouragementTexts.length)
            ];
    }, 5000);
};

// æ’­æ”¾èƒŒæ™¯éŸ³ä¹
const playBackgroundMusic = () => {
    try {
        // é¦–å…ˆå°è¯•åŠ è½½å¤–éƒ¨éŸ³é¢‘æ–‡ä»¶
        if (!audioElement) {
            audioElement = new Audio("/base.wav");
            audioElement.loop = true;
            audioElement.volume = 0.5;
        }

        // å°è¯•æ’­æ”¾å¤–éƒ¨éŸ³é¢‘
        audioElement.currentTime = 0;
        const playPromise = audioElement.play();

        if (playPromise !== undefined) {
            playPromise.catch((err) => {
                console.log("å¤–éƒ¨éŸ³é¢‘æ–‡ä»¶ä¸å­˜åœ¨æˆ–æ’­æ”¾å¤±è´¥ï¼Œä½¿ç”¨åˆæˆéŸ³ä¹:", err);
                // å¦‚æœå¤–éƒ¨éŸ³é¢‘å¤±è´¥ï¼Œä½¿ç”¨åˆæˆéŸ³ä¹
                playGeneratedMusic();
            });
        }
    } catch (error) {
        console.log("éŸ³é¢‘åŠ è½½å¤±è´¥ï¼Œä½¿ç”¨åˆæˆéŸ³ä¹:", error);
        playGeneratedMusic();
    }
};

// ä½¿ç”¨Web Audio APIç”ŸæˆèƒŒæ™¯éŸ³ä¹
let backgroundMusicInterval = null;
const playGeneratedMusic = () => {
    try {
        if (!audioContext) {
            audioContext = new (window.AudioContext ||
                window.webkitAudioContext)();
        }

        // å’Œå¼¦è¿›è¡Œï¼šC - Am - F - G
        const chordProgression = [
            [261.63, 329.63, 392.0], // C major (C, E, G)
            [220.0, 261.63, 329.63], // A minor (A, C, E)
            [174.61, 220.0, 261.63], // F major (F, A, C)
            [196.0, 246.94, 293.66], // G major (G, B, D)
        ];

        let chordIndex = 0;
        const bpm = 120;
        const beatDuration = 60 / bpm; // 0.5ç§’

        // æ’­æ”¾ä¸€ä¸ªå’Œå¼¦
        const playChord = () => {
            if (!audioContext) return;

            const now = audioContext.currentTime;
            const chord =
                chordProgression[chordIndex % chordProgression.length];

            // ä¸ºå’Œå¼¦ä¸­çš„æ¯ä¸ªéŸ³åˆ›å»ºæŒ¯è¡å™¨
            chord.forEach((frequency, index) => {
                const oscillator = audioContext.createOscillator();
                const gainNode = audioContext.createGain();

                oscillator.connect(gainNode);
                gainNode.connect(audioContext.destination);

                oscillator.frequency.value = frequency;
                oscillator.type = "sine";

                // è®¾ç½®éŸ³é‡åŒ…ç»œï¼ˆæ¸å…¥æ¸å‡ºï¼‰
                const volume = 0.03; // é™ä½éŸ³é‡é¿å…è¿‡å“
                gainNode.gain.setValueAtTime(0, now);
                gainNode.gain.linearRampToValueAtTime(volume, now + 0.05);
                gainNode.gain.linearRampToValueAtTime(
                    volume * 0.7,
                    now + beatDuration * 2 - 0.1,
                );
                gainNode.gain.linearRampToValueAtTime(
                    0.001,
                    now + beatDuration * 2,
                );

                oscillator.start(now);
                oscillator.stop(now + beatDuration * 2);
            });

            // æ·»åŠ èŠ‚æ‹é¼“ç‚¹
            const drumOsc = audioContext.createOscillator();
            const drumGain = audioContext.createGain();

            drumOsc.connect(drumGain);
            drumGain.connect(audioContext.destination);

            drumOsc.frequency.value = 80;
            drumOsc.type = "triangle";

            drumGain.gain.setValueAtTime(0.08, now);
            drumGain.gain.exponentialRampToValueAtTime(0.001, now + 0.1);

            drumOsc.start(now);
            drumOsc.stop(now + 0.1);

            chordIndex++;
        };

        // ç«‹å³æ’­æ”¾ç¬¬ä¸€ä¸ªå’Œå¼¦
        playChord();

        // æ¯2æ‹æ’­æ”¾ä¸€ä¸ªæ–°å’Œå¼¦
        backgroundMusicInterval = setInterval(playChord, beatDuration * 2000);
    } catch (error) {
        console.log("Web Audio API ä¸å¯ç”¨:", error);
    }
};

// åœæ­¢èƒŒæ™¯éŸ³ä¹
const stopBackgroundMusic = () => {
    // åœæ­¢å¤–éƒ¨éŸ³é¢‘
    if (audioElement) {
        audioElement.pause();
        audioElement.currentTime = 0;
    }

    // åœæ­¢åˆæˆéŸ³ä¹
    if (backgroundMusicInterval) {
        clearInterval(backgroundMusicInterval);
        backgroundMusicInterval = null;
    }
};

// ç”ŸæˆèŠ‚æ‹ï¼ˆæ¨¡æ‹ŸéŸ³é¢‘åˆ†æç»“æœï¼‰
const generateBeats = () => {
    beatSchedule = [];
    const bpm = 120; // æ¯åˆ†é’Ÿ120æ‹
    const beatInterval = (60 / bpm) * 1000; // æ¯«ç§’
    const totalBeats = 40; // æ€»å…±40ä¸ªèŠ‚æ‹

    const passwordChars = targetPassword.value.split("");
    const interferenceChars = [
        "x",
        "z",
        "q",
        "w",
        "k",
        "m",
        "r",
        "t",
        "u",
        "i",
        "o",
    ];

    let passwordCharIndex = 0;

    for (let i = 0; i < totalBeats; i++) {
        const time = i * beatInterval;
        const isLongNote = Math.random() < 0.2; // 20%æ¦‚ç‡é•¿éŸ³ç¬¦
        const duration = isLongNote ? beatInterval * 1.5 : beatInterval * 0.3;

        // 70%æ¦‚ç‡ç”Ÿæˆå¯†ç å­—ç¬¦ï¼Œ30%æ¦‚ç‡ç”Ÿæˆå¹²æ‰°å­—ç¬¦
        let char, isTarget;
        if (Math.random() < 0.7 && passwordCharIndex < passwordChars.length) {
            char = passwordChars[passwordCharIndex];
            isTarget = true;
            passwordCharIndex++;

            // ç¡®ä¿æ¯ä¸ªå¯†ç å­—ç¬¦éƒ½ä¼šå‡ºç°
            if (
                passwordCharIndex < passwordChars.length &&
                Math.random() < 0.5
            ) {
                passwordCharIndex--; // æœ‰æ—¶å€™ä¸å‰è¿›ï¼Œè®©å­—ç¬¦å¤šå‡ºç°å‡ æ¬¡
            }
        } else {
            char =
                interferenceChars[
                    Math.floor(Math.random() * interferenceChars.length)
                ];
            isTarget = false;
        }

        beatSchedule.push({
            time,
            char,
            isTarget,
            type: isLongNote ? "long" : "short",
            duration,
        });
    }
};

// æ¸¸æˆä¸»å¾ªç¯
const updateGame = () => {
    const currentTime = Date.now() - gameStartTime;

    // ç”Ÿæˆæ–°éŸ³ç¬¦
    while (
        currentBeatIndex < beatSchedule.length &&
        beatSchedule[currentBeatIndex].time <= currentTime + 2000
    ) {
        spawnNote(beatSchedule[currentBeatIndex]);
        currentBeatIndex++;
    }

    // æ›´æ–°éŸ³ç¬¦ä½ç½®
    const gameArea = document.querySelector(".game-area");
    if (!gameArea) return;

    const judgeLineY = gameArea.clientHeight - 100;
    const spawnY = -50;
    const fallDistance = judgeLineY - spawnY;
    const fallTime = 2000; // 2ç§’ä¸‹è½æ—¶é—´

    activeNotes.value.forEach((note) => {
        const elapsed = Date.now() - note.spawnTime;
        const progress = Math.min(elapsed / fallTime, 1);
        note.y = spawnY + fallDistance * progress;

        // æ£€æŸ¥æ˜¯å¦é”™è¿‡
        if (note.y > judgeLineY + 80 && !note.hit) {
            note.missed = true;
            if (
                note.isTarget &&
                note.char === targetPassword.value[currentPasswordIndex.value]
            ) {
                handleMiss(note);
            }
        }
    });

    // ç§»é™¤è¶…å‡ºå±å¹•çš„éŸ³ç¬¦
    activeNotes.value = activeNotes.value.filter(
        (note) => note.y < judgeLineY + 200,
    );

    // ç§»é™¤è¿‡æœŸçš„ç‰¹æ•ˆ
    hitEffects.value = hitEffects.value.filter(
        (effect) => Date.now() - effect.time < 1000,
    );

    // æ›´æ–°ç‰¹æ•ˆä½ç½®
    hitEffects.value.forEach((effect) => {
        const elapsed = Date.now() - effect.time;
        effect.y -= elapsed * 0.05; // å‘ä¸Šé£˜
    });

    // æ£€æŸ¥æ¸¸æˆç»“æŸ
    if (hp.value <= 0) {
        endGame(false);
    }

    // æ£€æŸ¥èƒœåˆ©
    if (currentPasswordIndex.value >= targetPassword.value.length) {
        endGame(true);
    }
};

// ç”ŸæˆéŸ³ç¬¦
const spawnNote = (beat) => {
    const gameArea = document.querySelector(".game-area");
    if (!gameArea) return;

    const trackWidth = gameArea.clientWidth / tracks.value.length;
    const trackIndex = Math.floor(Math.random() * tracks.value.length);

    const note = {
        id: noteIdCounter.value++,
        char: beat.char,
        isTarget: beat.isTarget,
        type: beat.type,
        x: trackIndex * trackWidth + trackWidth / 2 - 30,
        y: -50,
        height: beat.type === "long" ? 150 : 60,
        spawnTime: Date.now(),
        hit: false,
        missed: false,
        duration: beat.duration,
    };

    activeNotes.value.push(note);
};

// å¤„ç†éŸ³ç¬¦ç‚¹å‡»
const handleNoteClick = (note, event) => {
    if (note.hit || note.missed) return;

    const gameArea = document.querySelector(".game-area");
    if (!gameArea) return;

    const judgeLineY = gameArea.clientHeight - 100;
    const distance = Math.abs(note.y - judgeLineY);

    const clickX = event.clientX || event.touches[0].clientX;
    const clickY = event.clientY || event.touches[0].clientY;

    // åˆ¤å®šçª—å£
    let judgement = "";
    if (distance <= 50) {
        judgement = "perfect";
    } else if (distance <= 100) {
        judgement = "good";
    } else {
        judgement = "miss";
    }

    // æ£€æŸ¥æ˜¯å¦æ˜¯ç›®æ ‡å­—ç¬¦
    const targetChar = targetPassword.value[currentPasswordIndex.value];

    if (note.char === targetChar && judgement !== "miss") {
        // æ­£ç¡®çš„å­—ç¬¦
        note.hit = true;

        if (note.type === "long") {
            // é•¿æŒ‰éŸ³ç¬¦
            longPressNote = note;
            longPressTimer = setTimeout(() => {
                completeLongNote(note, judgement, clickX, clickY);
            }, note.duration);

            // ç›‘å¬æ¾å¼€
            const handleRelease = () => {
                if (longPressNote === note) {
                    clearTimeout(longPressTimer);
                    // æå‰æ¾å¼€ï¼Œåˆ¤å®šä¸ºmiss
                    handleMiss(note);
                    longPressNote = null;
                }
                document.removeEventListener("mouseup", handleRelease);
                document.removeEventListener("touchend", handleRelease);
            };
            document.addEventListener("mouseup", handleRelease);
            document.addEventListener("touchend", handleRelease);
        } else {
            // çŸ­éŸ³ç¬¦
            handleHit(note, judgement, clickX, clickY);
            currentPasswordIndex.value++;
        }
    } else if (note.char !== targetChar && judgement !== "miss") {
        // æ‰“é”™äº†å­—ç¬¦
        handleWrongChar(note, clickX, clickY);
    } else {
        // Miss
        handleMiss(note);
    }
};

// å®Œæˆé•¿æŒ‰éŸ³ç¬¦
const completeLongNote = (note, judgement, x, y) => {
    handleHit(note, judgement, x, y);
    currentPasswordIndex.value++;
    longPressNote = null;
};

// å¤„ç†å‡»ä¸­
const handleHit = (note, judgement, x, y) => {
    note.hit = true;

    if (judgement === "perfect") {
        perfectHits.value++;
        combo.value++;
        lastJudgement.value = "PERFECT!";
        showEffect("perfect", "Perfect!", x, y);
        playHitSound("perfect");
    } else if (judgement === "good") {
        goodHits.value++;
        combo.value++;
        lastJudgement.value = "GOOD";
        showEffect("good", "Good", x, y);
        playHitSound("good");
    }

    if (combo.value > maxCombo.value) {
        maxCombo.value = combo.value;
    }

    // Feveræ¨¡å¼
    if (combo.value >= 5) {
        encouragementText.value = "ğŸ”¥ FEVER MODE! ğŸ”¥";
    }
};

// å¤„ç†æ‰“é”™å­—ç¬¦
const handleWrongChar = (note, x, y) => {
    note.hit = true;
    combo.value = 0;
    hp.value = Math.max(0, hp.value - 10);
    lastJudgement.value = "WRONG!";
    showEffect(
        "miss",
        `é”™äº†ï¼æ˜¯ ${targetPassword.value[currentPasswordIndex.value]}`,
        x,
        y,
    );
    missHits.value++;
    playHitSound("miss");
};

// å¤„ç†Miss
const handleMiss = (note) => {
    if (note.missed) return;
    note.missed = true;
    combo.value = 0;
    hp.value = Math.max(0, hp.value - 15);
    lastJudgement.value = "MISS";
    missHits.value++;
    playHitSound("miss");

    // å±å¹•éœ‡åŠ¨æ•ˆæœ
    const gameArea = document.querySelector(".rhythm-password-game");
    if (gameArea) {
        gameArea.classList.add("shake");
        setTimeout(() => gameArea.classList.remove("shake"), 200);
    }
};

// æ˜¾ç¤ºç‰¹æ•ˆ
const showEffect = (type, text, x, y) => {
    hitEffects.value.push({
        id: effectIdCounter.value++,
        type,
        text,
        x,
        y,
        time: Date.now(),
    });
};

// ç»“æŸæ¸¸æˆ
const endGame = (success) => {
    if (gameLoop) {
        clearInterval(gameLoop);
        gameLoop = null;
    }

    // åœæ­¢èƒŒæ™¯éŸ³ä¹
    stopBackgroundMusic();

    if (success) {
        gameSuccess.value = true;
        gameFailed.value = false;
        // æ’­æ”¾æˆåŠŸéŸ³æ•ˆ
        playSuccessSound();
    } else {
        gameFailed.value = true;
        failMessage.value =
            failMessages[Math.floor(Math.random() * failMessages.length)];
        // æ’­æ”¾å¤±è´¥éŸ³æ•ˆ
        playFailSound();
    }
};

// é‡æ–°å¼€å§‹
const restartGame = () => {
    // åœæ­¢éŸ³ä¹
    stopBackgroundMusic();

    gameFailed.value = false;
    gameStarted.value = false;
    currentPasswordIndex.value = 0;
    combo.value = 0;
    maxCombo.value = 0;
    hp.value = 100;
    activeNotes.value = [];
    hitEffects.value = [];
    perfectHits.value = 0;
    goodHits.value = 0;
    missHits.value = 0;
};

// è·å–è¯„çº§
const getRating = () => {
    if (
        accuracy.value >= 95 &&
        perfectHits.value >= targetPassword.value.length
    ) {
        return "S çº§ - éŸ³æ¸¸ä¹‹ç¥ï¼";
    } else if (accuracy.value >= 85) {
        return "A çº§ - ä¼˜ç§€ï¼";
    } else if (accuracy.value >= 70) {
        return "B çº§ - è¿˜è¡Œ~";
    } else {
        return "C çº§ - å‹‰å¼ºé€šè¿‡";
    }
};

// å¤„ç†ç™»å½•æˆåŠŸ
const handleLoginSuccess = () => {
    emit("success", {
        password: targetPassword.value,
        accuracy: accuracy.value,
        combo: maxCombo.value,
        attempts: attempts.value,
    });
};

// æ’­æ”¾æˆåŠŸéŸ³æ•ˆ
const playSuccessSound = () => {
    try {
        if (!audioContext) {
            audioContext = new (window.AudioContext ||
                window.webkitAudioContext)();
        }

        const oscillator = audioContext.createOscillator();
        const gainNode = audioContext.createGain();

        oscillator.connect(gainNode);
        gainNode.connect(audioContext.destination);

        oscillator.frequency.setValueAtTime(523.25, audioContext.currentTime); // C5
        oscillator.frequency.setValueAtTime(
            659.25,
            audioContext.currentTime + 0.1,
        ); // E5
        oscillator.frequency.setValueAtTime(
            783.99,
            audioContext.currentTime + 0.2,
        ); // G5

        gainNode.gain.setValueAtTime(0.3, audioContext.currentTime);
        gainNode.gain.exponentialRampToValueAtTime(
            0.01,
            audioContext.currentTime + 0.5,
        );

        oscillator.start(audioContext.currentTime);
        oscillator.stop(audioContext.currentTime + 0.5);
    } catch (error) {
        console.log("éŸ³æ•ˆæ’­æ”¾å¤±è´¥:", error);
    }
};

// æ’­æ”¾å¤±è´¥éŸ³æ•ˆ
const playFailSound = () => {
    try {
        if (!audioContext) {
            audioContext = new (window.AudioContext ||
                window.webkitAudioContext)();
        }

        const oscillator = audioContext.createOscillator();
        const gainNode = audioContext.createGain();

        oscillator.connect(gainNode);
        gainNode.connect(audioContext.destination);

        oscillator.frequency.setValueAtTime(200, audioContext.currentTime);
        oscillator.frequency.exponentialRampToValueAtTime(
            100,
            audioContext.currentTime + 0.3,
        );
        oscillator.type = "sawtooth";

        gainNode.gain.setValueAtTime(0.2, audioContext.currentTime);
        gainNode.gain.exponentialRampToValueAtTime(
            0.01,
            audioContext.currentTime + 0.3,
        );

        oscillator.start(audioContext.currentTime);
        oscillator.stop(audioContext.currentTime + 0.3);
    } catch (error) {
        console.log("éŸ³æ•ˆæ’­æ”¾å¤±è´¥:", error);
    }
};

// æ’­æ”¾å‡»æ‰“éŸ³æ•ˆ
const playHitSound = (type) => {
    try {
        if (!audioContext) {
            audioContext = new (window.AudioContext ||
                window.webkitAudioContext)();
        }

        const oscillator = audioContext.createOscillator();
        const gainNode = audioContext.createGain();

        oscillator.connect(gainNode);
        gainNode.connect(audioContext.destination);

        if (type === "perfect") {
            oscillator.frequency.value = 880; // A5 - é«˜éŸ³
            gainNode.gain.setValueAtTime(0.15, audioContext.currentTime);
        } else if (type === "good") {
            oscillator.frequency.value = 660; // E5 - ä¸­éŸ³
            gainNode.gain.setValueAtTime(0.1, audioContext.currentTime);
        } else {
            oscillator.frequency.value = 220; // A3 - ä½éŸ³
            gainNode.gain.setValueAtTime(0.08, audioContext.currentTime);
        }

        gainNode.gain.exponentialRampToValueAtTime(
            0.01,
            audioContext.currentTime + 0.1,
        );

        oscillator.start(audioContext.currentTime);
        oscillator.stop(audioContext.currentTime + 0.1);
    } catch (error) {
        console.log("éŸ³æ•ˆæ’­æ”¾å¤±è´¥:", error);
    }
};

// æ¸…ç†
onUnmounted(() => {
    if (gameLoop) {
        clearInterval(gameLoop);
    }
    if (longPressTimer) {
        clearTimeout(longPressTimer);
    }
    // åœæ­¢éŸ³ä¹
    stopBackgroundMusic();
    // å…³é—­éŸ³é¢‘ä¸Šä¸‹æ–‡
    if (audioContext) {
        audioContext.close();
    }
});
